<template>
     <div id="home">
          <nav>
               <div id="tabs">
                    <router-link id="landing_container" to="/">
                         <img src="../assets/logo.svg" id="logo" />
                    </router-link>

                    <div id="span_container">
                         <router-link id="tab_dynamic" v-on:click="changeSection(0)" to="/home/">Home</router-link>
                         <router-link id="tab_explore" v-on:click="changeSection(1)" to="/home/explore">Explore</router-link>
                         <router-link id="tab_listing" v-on:click="changeSection(2)" to="/home/listing">Your Listing</router-link>
                         <router-link id="tab_lease" v-on:click="changeSection(3)" to="/home/lease" >Your Lease</router-link>
                         <router-link id="tab_account" v-on:click="changeSection(4)" to="/home/account">Account</router-link>
                         <svg id="line">
                              <line class="cls-1" x1="17" y1="2" x2="65" y2="2" />
                         </svg>
                    </div>

                    <div id="pfp_container">
                         <input id="search" />
                         <Pfp></Pfp>
                    </div>
               </div>
          </nav>

          <div id="view">
               <router-view></router-view>
          </div>
     </div>
</template>

<script>
import Pfp from "../components/Pfp.vue";
import Dynamic from "../views/Dynamic.vue";
import Explore from "../views/Explore.vue";
import Listing from "../views/Listing.vue";
import Lease from "../views/Lease.vue";
import Account from "../views/Account.vue";
export default {
     name: "Home",
     data() {
          return {
               section: 0
          };
     },
     methods: {
          changeSection(n) {
               switch (this.section) {
                    case 0:
                         $("#tab_dynamic").removeClass("active");
                         break;
                    case 1:
                         $("#tab_explore").removeClass("active");
                         break;
                    case 2:
                         $("#tab_listing").removeClass("active");
                         break;
                    case 3:
                         $("#tab_lease").removeClass("active");
                         break;
                    case 4:
                         $("#tab_account").removeClass("active");
                         break;
               }
               switch (n) {
                    case 0:
                         $("#tab_dynamic").addClass("active");
                         // anime({
                         //      targets: "#line",
                         //      left: "0%",
                         //      duration: 100,
                         //      easing: "easeOutCubic"
                         // });
                         break;
                    case 1:
                         $("#tab_explore").addClass("active");
                         break;
                    case 2:
                         $("#tab_listing").addClass("active");
                         break;
                    case 3:
                         $("#tab_lease").addClass("active");
                         break;
                    case 4:
                         $("#tab_account").addClass("active");
                         break;
               }
               this.section = n;
          }
     },
     computed: {
          width: function() {
               let x = window.innerWidth * 0.5;
               console.log(x);
               return x;
          }
     },
     mounted() {
          this.changeSection(0);
     },
     components: {
          Pfp,
          Account,
          Dynamic,
          Explore,
          Listing,
          Lease
     }
};
</script>

